.form {
  display: flex;
  gap: 8px;
  margin-bottom: 20px;
  width: 30%;
}

.input {
  flex-grow: 1;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color .3s ease;
}

.button:hover {
  background-color: #0056b3;
}
